import React, { useEffect, useState } from "react";
import { useNavigate, useLocation } from "react-router-dom";

// views
import { Info, Application, ApplicationInfo } from "./index";

// components

// styles

// images

// logics

const Main = ({ match, history }) => {
   const location = useLocation();
   console.log(location.pathname)
   console.log(location.pathname.split('/contest/fashion')[1])
   const [state, setState] = useState({
      current:"",
   });
   useEffect(() => {
      setState({
         ...state,
         current: location.pathname.split('/contest/fashion')[1],
      });
      console.log(location.pathname)
   }, [location.pathname]);
   return (
      <>
      {
         state.current === "/info" ?
         <Info />  :null
      }
         {
         state.current === "/application" ?
         <Application />  :null
         }
         {
         state.current === "/application_info" ?
         <ApplicationInfo />  :null
      }
      </>
      
   );
};

export default Main;
