import styled from "styled-components";

// Main 
export const Container = styled.div`
/*  padding-top:80px; 는 header 때문에 꼭 필요한 요소 입니다. */
  padding-top:80px;
  background-color: #fff;
  color:#000;
  section {
    width:100%;
    max-width: 960px;
    margin:0 auto;
    box-sizing: border-box;
    h3 {
      margin-bottom: 18px;
      color:#616161;
      font-size:14px;
      font-weight: 400;
      position: relative;
      &::before {
        content: '';
        position: absolute;
        width:1px;
        height: 8px;
        top:1px; left:0;
        transform: rotate(45deg);
        display: block;
        background-color: #616161;
      }
      &::after {
        content: '';
        position: absolute;
        width:1px;
        height: 8px;
        top:6px; left:0;
        transform: rotate(-45deg);
        display: block;
        background-color: #616161;
      }
      a {
        display: block;
        width: 80px;
        padding-left: 10px;
      }
    }
    h2 {
      margin-bottom: 10px;
      font-size: 24px;
    }
    strong {
      font-size: 14px;
      color:#616161;
      margin-bottom: 18px;
      font-weight: 400;
      display: block;
      border-bottom: 1px solid #E3E3E3;
      padding-bottom: 18px;
    }
    .text {
      p {
        margin-bottom: 30px;
        line-height: 24px;
      }
    }
    .notice {
      img {
        width:305px;
        height: 354px;
        margin-bottom: 30px;
      }
    }
    ul {
      li {
        padding:18px 22px;
        img {
          width:16px;
          height: 19px;
          margin-right:10px;
          display: inline-block;
          vertical-align: middle;
        }
        a {
          display: block;
          font-size: 14px;
          color:#616161;
        }
        &:last-child {
          border-bottom: 1px solid #E3E3E3;
        }
      }
    }
    .lastlist {
      margin-top:18px;
      padding-bottom: 100px;
    }
  }

`