import React, { useEffect ,useState} from "react";
import  { useNavigate, useLocation} from "react-router-dom";
// styles
import styled from "styled-components";

// images

const Footer = () => {
    const navigate = useNavigate();
    const location = useLocation();
    const [state, setState] = useState({
       current:"",
    });
    useEffect(() => {
       setState({
          ...state,
          current: location.pathname,
       });
    }, [location.pathname]);
   return (
      <>
      {state.current ==="/"? null :
       <FooterView>
         <footer>
            <h2><img src={require("./footer_logo.png")} alt="footer_logo" /></h2>
            <ul>
                  <li><a href="#">개인정보처리방침</a></li>
                  <li><a href="#">이용약관</a></li>
            </ul>
            <ul>
                  <li>(사)고교패션콘테스트위드이상봉</li>
                  <li>대표 : 이상봉</li>
                  <li>서울 강남구 도산대로 451 (청담동) 청담에디션 6F 고교패션콘테스트 사무국</li>
                  <li>고객센터 : 070-4214-7845</li>
                  <li>사업자등록번호 : 102-82-65931</li>
                  <li>Copyright (사)고교패션콘테스트위드이상봉. All rights reserved.</li>
            </ul>
         </footer> 
      </FooterView>
      }
      </>
     
  );
};
const FooterView = styled.footer`
  footer {
    width:100%;
    max-width: 1440px;
    margin: 0 auto;
    padding:62px 120px;
    box-sizing: border-box;
    h2 {
        width: 85px;
        height: 54px;
        display: inline-block;
        vertical-align: top;
    }
    ul {
        margin-left:60px;
        &:nth-child(2) {
            display: inline-block;
        }
        li {
            display: inline-block;
            margin-right:28px;
            a {
                font-weight: 700;
                color:#fff;
            }
        }
        &:last-child {
            margin-left:145px;
            li {
                display: block;
                margin-bottom: 10px;
                position: relative;
                &:last-child {
                    margin-bottom: 0;
                }
                &:nth-child(1), &:nth-child(2), &:nth-child(4), &:nth-child(5) {
                    display: inline-block;
                }
                &:nth-child(4) {
                    &::before {
                        content: '';
                        display: block;
                        width:1px;
                        height: 15px;
                        background-color: #fff;
                        position: absolute;
                        right:-18px;
                        top:3px
                    }
                }
            }
        }
    }
}
`;

export default Footer;
