import styled from "styled-components";


// Main 
export const Container = styled.div`
      width:100%;
      max-width: 960px;
      margin:0 auto;
      box-sizing: border-box;
      padding-top:80px;
      .underline {
         font-size: 34px;
         color:#fff;
         margin-bottom: 18px;
         position: relative;
         display: table;
         z-index: 9;
         &::before {
            content: '';
            position: absolute;
            width:100%;
            height: 12px;
            background-color: #ffe600c0;
            display: block;
            left:0; bottom: 0;
            z-index: -1;
         }
      }
      .con3 {
         ul {
            li {
                  display: flex;
                  margin-bottom: 20px;
                  .left {
                     width:25%;
                     display: inline-block;
                  }
                  .right {
                     width:75%;
                     display: inline-block;
                  }
                  p {
                     line-height: 34px;
                  }
                  span {
                     height: 34px;
                     background-color: #fff;
                     color:#000;
                     /* pad ding:6px 8px; */
                     display: block;
                     font-weight: 700;
                     text-align: center;
                     line-height: 34px;
                  }
                  &:nth-child(1) .left span{
                     width:88px;
                  }
                  &:nth-child(2) .left span{
                     width:73px;
                  }
                  &:nth-child(3) .left span{
                     width:116px;
                  }
                  &:nth-child(4) .left span{
                     width:198px;
                  }
                  &:nth-child(5) .left span{
                     width:78px;
                  }
                  &:nth-child(6) .left span{
                     width:153px;
                  }
            }
         }
      }
      .con4 {
            span {
               display: block;
               margin: 18px 0;
            }
            p {
            margin-bottom: 10px;   
            }
      }
      .application {
            >div {
               padding:20px;
               background-color: rgba(255, 255, 255, 0.2);
               border-radius: 10px;
               margin-bottom: 18px;
               span {
                     width:26px;
                     height: 26px;
                     background-color: #fff;
                     line-height: 26px;
                     text-align: center;
                     display: inline-block;
                     color:#000;
                     border-radius: 100%;
                     vertical-align: top;
               }
               .text {
                     display: inline-block;
                     margin-left:14px;
                     >a {
                        font-size: 14px;
                     }
                     p {
                        margin-bottom: 5px;
                        font-weight: 700;
                        &:last-child {
                           margin-bottom: 0;
                           font-size: 14px;
                           font-weight: 400;
                           line-height: 22.5px;
                        }
                     }
               }
               .link {
                     display: block;
                     float: right;
                     padding:10px 8px;
                     background-color: #747474;
                     border-radius: 5px;
                     font-size:14px;
               }
            }
      }
      .note {
         ul {
            li {
                  line-height: 27px;
            }
         }
      }

`