import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";

//lib
import { ScrollToTop } from "Utils/Lib/helper";

// views
import {
  Main,
  Contest,
  Information,
  Gallery,
  Notice,
  NoticeDetail,
} from "Views";

// components
import { Header, Footer } from "Components";

// style
import GlobalStyle from "./Styles/global";

const App = () => {
  return (
    <BrowserRouter basename="/liesangbong_highschoolfashion">
      <ScrollToTop />
      <GlobalStyle />
      <Header />
      <Routes>
        <Route path="/" element={<Main />} />
        <Route path="/contest/:type/:location" element={<Contest />} />
        <Route path="/information/:type" element={<Information />} />
        <Route path="/gallery/:year" element={<Gallery />} />
        <Route path="/notice/:type" element={<Notice />} />
        <Route path="/notice/:type/:id" element={<NoticeDetail />} />
      </Routes>
      <Footer />
    </BrowserRouter>
  );
};

export default App;
