import React, { useEffect, useState } from "react";
import {  useLocation } from "react-router-dom";

// views
import { Fashion, Model } from "./index";

// components

// styles

// images

// logics

const Contest = () => {
   const location = useLocation();
   const [state, setState] = useState({
      current:"",
   });
   useEffect(() => {
      setState({
         ...state,
         current: location.pathname.split('/contest/')[1],
      });
      console.log(location.pathname)
   }, [location.pathname]);
   return (
      <>
         {location.pathname.split('/contest/')[1].includes("fashion")?
               <Fashion /> :
               <Model />
         }
      </>
      
   );
};

export default Contest;
