import React, { useEffect, useState } from "react";
import { useNavigate, useLocation } from "react-router-dom";
// styles
import styled from "styled-components";

// images
import Logo from "Assets/images/logo.png";

const Header = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const [state, setState] = useState({
    current: "",
  });
  useEffect(() => {
    setState({
      ...state,
      current: location.pathname,
    });
  }, [location.pathname]);
  // console.log(location.pathname)
  return (
    <HeaderView current={location.pathname}>
      <div className="inner">
        <h2 onClick={() => navigate("/")}>
          <img src={Logo} alt="header_로고" />
        </h2>
        <ul className="nav">
          <li
            className={
              location.pathname.includes("/contest/fashion/") ? "on" : ""
            }
          >
            <h3 onClick={() => navigate("/contest/fashion/info")}>
              고교패션콘테스트
            </h3>
            <ul className="sub">
              <li onClick={() => navigate("/contest/fashion/info")}>
                콘테스트 소개
              </li>
              <li onClick={() => navigate("/contest/fashion/application_info")}>
                접수안내
              </li>
              <li onClick={() => navigate("/contest/fashion/application")}>
                접수하기
              </li>
            </ul>
          </li>
          <li
            className={
              location.pathname.includes("/contest/model/") ? "on" : ""
            }
          >
            <h3 onClick={() => navigate("/contest/model/info")}>
              고교모델콘테스트
            </h3>
            <ul className="sub">
              <li onClick={() => navigate("/contest/model/info")}>
                콘테스트 소개
              </li>
              <li onClick={() => navigate("/contest/model/application_info")}>
                접수안내
              </li>
              <li onClick={() => navigate("/contest/model/application")}>
                접수하기
              </li>
            </ul>
          </li>
          <li
            className={location.pathname.includes("/information/") ? "on" : ""}
          >
            <h3 onClick={() => navigate("/information/summary")}>소개</h3>
            <ul className="sub">
              <li onClick={() => navigate("/information/summary")}>개요</li>
              <li onClick={() => navigate("/information/location")}>
                오시는 길
              </li>
            </ul>
          </li>
          <li className={location.pathname.includes("/gallery/") ? "on" : ""}>
            <h3 onClick={() => navigate("/gallery/2021")}>갤러리</h3>
            <ul className="sub">
              <li onClick={() => navigate("/gallery/2021")}>
                2021년 제6회 콘테스트
              </li>
              <li onClick={() => navigate("/gallery/2020")}>
                2020년 제5회 콘테스트
              </li>
              <li onClick={() => navigate("/gallery/2019")}>
                2019년 제4회 콘테스트
              </li>
              <li onClick={() => navigate("/gallery/2018")}>
                2018년 제3회 콘테스트
              </li>
              <li onClick={() => navigate("/gallery/2017")}>
                2017년 제2회 콘테스트
              </li>
              <li onClick={() => navigate("/gallery/2016")}>
                2016년 제1회 콘테스트
              </li>
            </ul>
          </li>
          <li className={location.pathname.includes("/notice/") ? "on" : ""}>
            <h3 onClick={() => navigate("/notice/notice")}>공지사항</h3>
            <ul className="sub">
              <li onClick={() => navigate("/notice/notice")}>공지사항</li>
              <li onClick={() => navigate("/notice/news")}>보도자료</li>
            </ul>
          </li>
          <li className="appgo">
            <a href="#">
              <img src={require("./google_play.png")} alt="main_kakaoimg" />
            </a>
          </li>
          <li className="appgo">
            <a href="#">
              <img src={require("./apple.png")} alt="main_kakaoimg" />
            </a>
          </li>
        </ul>
      </div>
    </HeaderView>
  );
};
const HeaderView = styled.div`
  width: 100%;
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  background-color: ${({ current }) =>
    current === "/" ? "transparent" : "#000000"};
  &:hover {
    background-color: #000;
  }
  .inner {
    max-width: 1440px;
    margin: 0 auto;
    z-index: 999;
    padding: 3px 60px;
    display: grid;
    grid-template-columns: 1fr 2fr;
    h2 {
      display: inline-block;
      cursor: pointer;
      img {
        width: 258px;
        height: 17px;
        display: block;
        margin-top: 25px;
      }
    }
    .nav {
      text-align: right;
      display: inline-block;
      vertical-align: top;
      > li {
        vertical-align: top;
        display: inline-block;
        text-align: center;
        line-height: 40px;
        position: relative;
        h3 {
          cursor: pointer;
          display: block;
          font-weight: 400;
          font-size: 16px;
          padding: 26px 15px;
        }
        .sub {
          opacity: 0;
          display: none;
          position: absolute;
          bottom: 0;
          left: 50%;
          transform: translate(-50%, 100%);
          padding: 16px 30px;
          box-sizing: border-box;
          background-color: rgba(0, 0, 0, 0.7);
          min-width: 100%;
          li {
            cursor: pointer;
            display: block;
            height: 40px;
            line-height: 40px;
            font-size: 14px;
            white-space: nowrap;
          }
        }
        &.on {
          h3 {
            font-weight: 700;
          }
        }
        &:hover {
          .sub {
            opacity: 1;
            display: block;
          }
        }
      }
      .appgo {
        display: inline-block;
        position: relative;
        width: 40px;
        height: 40px;
        background: rgba(255, 255, 255, 0.3);
        border-radius: 100%;
        margin: 0 30px;
        margin-top: 14px;
        img {
          width: 60%;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translateX(-50%) translateY(-50%);
        }
        &:last-child {
          margin: 0;
          margin-top: 14px;
          img {
            width: 50%;
          }
        }
      }
    }

    a {
      color: #fff;
    }
  }
`;

export default Header;
