import React, { useRef } from "react";
import { Link } from "react-scroll";

// components

// styles
import { Container } from "./Info.style";
import { TitleSection, ContentSection } from "Styles/styles";

// images

// logics
import MainLogic from "./Info.logic";

const Main = ({ match, history }) => {
   const gnb = useRef();
   const { state, func } = MainLogic({ history, match });
   window.addEventListener('scroll', (e) => {
      console.log(window.scrollY)
      if (window.scrollY <150) {
         gnb.current.style="position:inherit; padding-top:0;"
      } else if (window.scrollY > 150) {
         gnb.current.style="position:fixed; top:74px;"
      }
   })
   return (
      <Container>
         <TitleSection>
            <h4 className="main_name">고교모델콘테스트</h4>
            <h2 className="intro">콘테스트 소개</h2>
            <ul className="gnb" id="gnb" ref={gnb}>
               <li><Link to="con1" activeClass="active" spy={true} smooth={true} offset={-280} >설명</Link></li>
               <li><Link to="con2" activeClass="active" spy={true} smooth={true} offset={-280} >응모자격</Link></li>
               <li><Link to="con3" activeClass="active" spy={true} smooth={true} offset={-280} >시상</Link></li>
               <li><Link to="con6" activeClass="active" spy={true} smooth={true} offset={-280} >주관 및 주최</Link></li>
            </ul>
         </TitleSection>
         <ContentSection>
            <div className="con1" id="con1">
                  <h2 className="underline">고교모델콘테스트 설명</h2>
                  <p>고교모델콘테스트는 2016년 이상봉 디자이너가~~</p>
            </div>
            <div className="con2" id="con2">
                  <h2 className="underline">응모자격</h2>
                  <ul>
                     <li>• 전국 고등학생</li>
                     <li>• 특성화 고등학교, 인문계 고등학교 및 자퇴생 가능</li>
                     <li>• 남자 178cm, 여자 168cm 이상</li>
                  </ul>
            </div>
            <div className="con3" id="con3">
                  <h2 className="underline">시상</h2>
                  <table>
                     <thead>
                        <tr>
                              <td>시상명</td>
                              <td>시상금</td>
                              <td>시상원</td>
                        </tr>
                     </thead>
                     <tbody>
                        <tr>
                              <td>성동구청장상</td>
                              <td>2,000,000</td>
                              <td>1</td>
                        </tr>
                        <tr>
                              <td>아시아모델페스티벌조직위원회장상</td>
                              <td>1,000,000</td>
                              <td>2(남,여 각 1명)</td>
                        </tr>
                        <tr>
                              <td>한국모델협회상</td>
                              <td>500,000</td>
                              <td>2(남,여 각 1명)</td>
                        </tr>
                        <tr>
                              <td>배달의민족상</td>
                              <td>500,000</td>
                              <td>2(남,여 각 1명)</td>
                        </tr>
                        <tr>
                              <td>한국패션산업협회상</td>
                              <td rowSpan="13" className="border">
                                 20만 원 x 6명 <br />
                                 10만 원 x 8명
                              </td>
                              <td>2(남,여 각 1명)</td>
                        </tr>
                        <tr>
                              <td>한문화진흥협회상</td>
                              <td>2(남,여 각 1명)</td>
                        </tr>
                        <tr>
                              <td>한국업스타일전문가협회상</td>
                              <td>2(남,여 각 1명)</td>
                        </tr>
                        <tr>
                              <td>푸른나무재단상</td>
                              <td>1</td>
                        </tr>
                        <tr>
                              <td>이상봉상</td>
                              <td>1</td>
                        </tr>
                        <tr>
                              <td>엠씨스퀘어상</td>
                              <td>2(남,여 각 1명)</td>
                        </tr>
                        <tr>
                              <td>더스타상</td>
                              <td>2(남,여 각 1명)</td>
                        </tr>
                        <tr>
                              <td>MBC아카데미상</td>
                              <td>2(남,여 각 1명)</td>
                        </tr>
                        <tr>
                              <td>모리스상</td>
                              <td>2(남,여 각 1명)</td>
                        </tr>
                        <tr>
                              <td>서경대학교상</td>
                              <td>2(남,여 각 1명)</td>
                        </tr>
                        <tr>
                              <td>더블유타임즈상</td>
                              <td>2(남,여 각 1명)</td>
                        </tr>
                        <tr>
                              <td>베스트포즈상</td>
                              <td>2(남,여 각 1명)</td>
                        </tr>
                        <tr>
                              <td>고교모델상</td>
                              <td></td>
                        </tr>
                     </tbody>
                  </table>
                  <p>※ 올해 해당 기관의 사정에 따라 상금 변동 및 추가 혹은 미지급될 수 있습니다.</p>
            </div>
            <div className="con6" id="con6">
                  <h2 className="underline">주관 및 주최</h2>
                  <div className="inner">
                     <div>
                        <span><img src={require("./img/host1.png")} alt="con6_주최1" /></span>
                        <p><b>주관</b> 성동문화재단</p>
                     </div>
                     <div>
                        <span className="lastbox"><img src={require("./img/host2.png")} alt="con6_주최2" /></span>
                        <p><b>주최</b> (사)고교패션콘테스트위드이상봉</p>
                     </div>
                  </div>
            </div>
         </ContentSection>
      </Container>
   );
};

export default Main;
