import React, { useRef } from "react";
import { Link } from "react-scroll";

// components

// styles
import { Container } from "./ApplicationInfo.style";
import { TitleSection, ContentSection } from "Styles/styles";

// images

// logics
import MainLogic from "./ApplicationInfo.logic";

const Main = ({ match, history }) => {
   const gnb = useRef();

   const { state, func } = MainLogic({ history, match });
   window.addEventListener('scroll', (e) => {
      // console.log(window.scrollY)
      if (window.scrollY < 150) {
         gnb.current.style="position:inherit; padding-top:0;"
      } else if (window.scrollY > 150) {
         gnb.current.style="position:fixed; top:74px;"

      }
   })
   return (
      <Container>
         <TitleSection>
            <h4 className="main_name">고교패션콘테스트</h4>
            <h2 className="intro">접수 안내</h2>
            <ul className="gnb" id="gnb" ref={gnb}>
               <li><Link to="con1" activeClass="active" spy={true} smooth={true} offset={-280} >작품주제</Link></li>
               <li><Link to="con2" activeClass="active" spy={true} smooth={true} offset={-280} >작품 제작 시 유의사항</Link></li>
               <li><Link to="con3" activeClass="active" spy={true} smooth={true} offset={-280} >콘테스트 일정</Link></li>
               <li><Link to="con4" activeClass="active" spy={true} smooth={true} offset={-280} >응모기간</Link></li>
               <li><Link to="application" activeClass="active" spy={true} smooth={true} offset={-280} >응모방법</Link></li>
               <li><Link to="ex" activeClass="active" spy={true} smooth={true} offset={-280} >디자인 앱 예시</Link></li>
               <li><Link to="con6" activeClass="active" spy={true} smooth={true} offset={-280} >작품반출 및 기타사항</Link></li>
                     </ul>
         </TitleSection>
         <ContentSection>
            <div className="con1" id="con1">
                  <h2 className="underline">작품 주제</h2>
                  <p>아래의 주제 중 하나를 선택합니다</p>
                  <ul>
                     <li>
                        <span>1</span><strong>친환경</strong>
                        <p>해양환경, 업사이클, 리사이클</p>
                     </li>
                     <li>
                        <span>2</span><strong>한글과 한복</strong>
                        <p>해양환경, 업사이클, 리사이클</p>
                     </li>
                     <li>
                        <span>3</span><strong>스마트문화도시</strong>
                        <p>해양환경, 업사이클, 리사이클</p>
                     </li>
                     <li>
                        <span>4</span><strong>충무공 이순신</strong>
                        <p>해양환경, 업사이클, 리사이클</p>
                     </li>
                     <li>
                        <span>5</span><strong>모나미룩</strong>
                        <p>해양환경, 업사이클, 리사이클</p>
                     </li>
                  </ul>
            </div>
            <div className="con2" id="con2">
                  <h2 className="underline">작품 제작 시 유의사항</h2>
                  <p>※ 1차 디자인맵 합격자를 대상으로 실물 멘토링과 본선 작품 제작이 진행됩니다.</p>
                  <p>※ 디자인맵 작품 양식은 일러스트레이터, 태블릿, 그래픽 디자인 등 어떠한 제한도 없이 가능하지만 락카, 혹은 입체적인 모형물 등 관리에 어려운 작품은 제한합니다.</p>
                  <p>※ 디자인맵은 1인당 최대 2개의 작품까지 제출 가능합니다.(단, 작품명은 다르게 제출해야 합니다.)</p>
                  <p>※ 입을 수 있는 의상 작품 제작을 원칙으로 합니다.</p>
            </div>
            <div className="con3" id="con3">
                  <h2 className="underline">콘테스트 일정</h2>
                  <ul>
                     <li>
                        <div className="left">
                              <span>사전설명회</span>
                        </div>
                        <p className="right">2022년 5월 14일 18:00 (고교패션콘테스트 유튜브 공식 채널)</p>
                     </li>
                     <li>
                        <div className="left">
                              <span>참가접수</span>
                        </div>
                        <p className="right">2022년 5월 16일 ~ 6월 30일 홈페이지 접수 (하단 첨부자료 다운로드)</p>
                     </li>
                     <li>
                        <div className="left">
                              <span>1차 합격자 발표</span>
                        </div>
                        <p>2022년 7월 초 예정 (홈페이지 공지사항)</p>
                     </li>
                     <li>
                        <div className="left">
                              <span>멘토링데이(1차 합격자 대상)</span>
                        </div>
                        <p className="right"> 2022년 7월 ~ 8월 예정 (홈페이지 공지사항)</p>
                     </li>
                     <li>
                        <div className="left">
                              <span>실물심사</span>
                        </div>
                        <p>2022년 9월 17일(토) 서울 성동구 소월아트홀 대극장</p>
                     </li>
                     <li>
                        <div className="left">
                              <span>본선 패션쇼 및 시상식</span>
                        </div>
                        <p>2022년 9월 18일(일) 서울 성동구 소월아트홀 대극장</p>
                     </li>
                  </ul>
                  <p>※ 코로나 상황에 따라 일정이 변경될 수 있습니다.</p>
            </div>
            <div className="con4" id="con4">
                  <h2 className="underline">응모 기간</h2>
                  <span>• 2022년 5월 16일(월) ~ 7월 2일(토) 19:00까지</span>
                  <p>※ 마감 이후 추가 접수 혹은 수정은 불가합니다.</p>
                  <p>※ 잘못된 정보 입력으로 인한 책임은 출품자에게 있습니다.</p>
            </div>
            <div className="application" id="application">
                  <h2 className="underline">응모 방법</h2>
                  <div>
                     <span>1</span>
                     <div className="text">
                        <p>구글 폼 신청서 작성</p>
                        <a href="#">https://forms.gle/wACQW8M2PjaT4RDm9</a>
                     </div>
                     <a href="#" className="link">작성하기</a>
                  </div>
                  <div>
                     <span>2</span>
                     <div className="text">
                        <p>참가신청서, 개인정보 수집활용 동의서, 디자인 맵 작성</p>
                        <p>작성 후 사무국으로 방문 접수 혹은 우체국을 통해 발송해주세요</p>
                     </div>
                     <a href="#" className="link">파일 다운로드</a>
                  </div>
                  <p>• 사무국 주소 : 서울 강남구 도산대로 451, 이상봉 6F 고교패션콘테스트 사무국</p>
            </div>
            <div className="ex" id="ex">
               <h2 className="underline">디자인 맵 예시</h2>
               <p>• 규격 : A3 사이즈 (가로 420mm / 세로  297mm) 총 2면</p>
               <p>• 반드시 안쪽의 접착부분에 제본 라벨을 붙여 양면을 접을 수 있도록 함 (응모 방법의 파일 다운로드 후 디자인 맵 파일 참고)</p>
                  <ul>
                     <li><img src={require("./img/design_ex1.png")} alt="디자인예시1" /></li>
                     <li><img src={require("./img/design_ex2.png")} alt="디자인예시2" /></li>
                     <li><img src={require("./img/design_ex3.png")} alt="디자인예시3" /></li>
                     <li><img src={require("./img/design_ex4.png")} alt="디자인예시4" /></li>
                     <li><img src={require("./img/design_ex5.png")} alt="디자인예시5" /></li>
                     <li><img src={require("./img/design_ex6.png")} alt="디자인예시6" /></li>
                     <li><img src={require("./img/design_ex7.png")} alt="디자인예시7" /></li>
                     <li><img src={require("./img/design_ex8.png")} alt="디자인예시8" /></li>
                  </ul>
            </div>
            <div className="con6" id="con6">
               <h2 className="underline">작품 반출 및 기타사항</h2>
               <ul>
                  <li>※ 출품된 1차 디자인맵은 반환되지 않습니다.</li>
                  <li>※ 의상 작품 반출은 패션쇼가 끝난 직후에 실시하며, 미 반출 작품은 책임지지 않습니다.</li>
                  <li>※ 상위 수상 작품은 향후 전시 등의 일정으로 인해 일정기간 본 (사)고교패션콘테스트위드이상봉으로 제작물이 귀속됩니다.</li>
                  <li>※ 저작권 침해 등 법적 책임은 출품자에게 있습니다.</li>
                  <li>※ 타인의 지적재산권을 침해하거나 표절 등의 사실이 확인된 경우 선발 취소, 상금 환수 조치를 시행합니다.</li>
                  <li>※ 공모전에 출품된 응모작의 저작권(저작재산권과 저작인격권)은 저작자인 출품자에게 원시적으로 귀속됩니다.(저작권법 제10조)</li>
                  <li>※ 공모전 주최자는 입상하지 않은 응모작에 대해서는 어떠한 권리도 취득 할 수 없습니다. 공모전 주최자는 별도의 반환 요청이 없는 한 입상하지 않은 응모작을 공모전 종료일로부터 3개월 이내에 모두 폐기하여야 합니다.</li>
                  <li>※ 공모전 주최자는 응모작을 접수할 때 응모작 반환 여부에 대한 응모자의 의사를 확인하는 절차를 마련하여야 합니다. 응모자가 저작물의 반환을 요구하는 경우에는 응모자에게 반환하되, 반환과 관련한 추가 비용이 발생하는 경우, 응모자가 부담할 수 있습니다.</li>
               </ul>
            </div>
         </ContentSection>
         
      </Container>
   );
};

export default Main;
