import React, { useState,useEffect} from "react";
import { useNavigate, useLocation } from "react-router-dom";

// components

// styles
import { Container ,MainVideo, MainbgImg,MainImg} from "./Main.style";

// images
import video1 from "./img/main _video.mp4"

// logics
import MainLogic from "./Main.logic";

const Main = ({ match, history }) => {
   const navigate = useNavigate();
   const location = useLocation();
   window.addEventListener('scroll', () => {
      var scrollDepth = Math.floor(((window.scrollY + window.innerHeight) / document.body.scrollHeight) * 100);
      console.log(scrollDepth)
      let viedoScale = Math.ceil(((scrollDepth/55)+ 0.4)*100)/100
      document.getElementById("videoSection").style.opacity = ((100 - scrollDepth) - 25) / 41;
      document.getElementById("video").style.transform = `scale(${viedoScale > 1.5 ? 1.5 : viedoScale})`;
      document.getElementById("leftimg").style.opacity =( scrollDepth - 70) /20;
      document.getElementById("leftimg").style.top =`${ scrollDepth >90? 0 : scrollDepth - 90 }%`;
      document.getElementById("rightimg").style.opacity =( scrollDepth - 70) /20;
      document.getElementById("rightimg").style.bottom =`${ scrollDepth >90? 0 : scrollDepth - 90 }%`;
      document.getElementById("line").style.opacity = (scrollDepth - 75) / 15;
      document.getElementById("line").style.transform = `translateX(-50%) translateY(-50%) rotate(${(scrollDepth>90?-15:((100-scrollDepth)-8)*2)}deg) scale(0.5)`;
      if (scrollDepth < 69) {
         document.getElementById("videoSection").style.display = "block";
      } else if (scrollDepth === 69||scrollDepth > 69) {
         document.getElementById("videoSection").style.display="none";
      }
      if (scrollDepth < 95) {
         document.getElementById("inner1").style.opacity="0";
         document.getElementById("inner2").style.opacity="0";
      } else if (scrollDepth === 95 || scrollDepth > 95) {
         document.getElementById("inner1").style.opacity="1";
         document.getElementById("inner2").style.opacity="1";
      }
   })
   // const { state, func} = MainLogic({ history, match });
   return (
         <Container className="scroll">
         <MainVideo id="videoSection">
            <video
               className="video"
               src={video1}
               autoPlay
               loop
               muted
               id="video"
            >
            </video>        
            <div className="text">
               패션 디자이너, 모델, <br />
            꿈을 펼쳐보세요
            </div>
            <img className="arrow" src={require("./img/arrow.png")} alt="스크롤 다운"/>
            
         </MainVideo>
         <ul className="social">
            <li><a href="#"><img src={require("./img/kakao_talk.png")} alt="main_kakaoimg" /></a></li>
            <li><a href="#"><img src={require("./img/youtube.png")} alt="main_kakaoimg" /></a></li>
         </ul> 
         <MainbgImg />
         <MainImg id="MainImg">
            <span id="line"></span>
            <div className="left">
               <img  className="leftimg" id="leftimg" src={require("./img/left.png")} alt="main_left"/>
               <div id="inner1" className="inner">
                  <strong>2022년</strong>
                  <img src={require("./img/fashion_logo.png")} alt="main_kakaoimg" />
                  <small>패션 디자이너의 꿈을 가진 청소년</small>
                  <p>2022.04.01 ~ 04.16</p>
                  <ul>
                     <li onClick={() => navigate('/contest/fashion/info')}>콘테스트 소개</li>
                     <li onClick={() => navigate('/contest/fashion/application_info')}>접수 안내</li>
                     <li onClick={() => navigate('/contest/fashion/application')}>접수하기</li>
                  </ul>
               </div>
            </div>
            <div className="right">
            <img className="rightimg" id="rightimg" src={require("./img/right.png")} alt="main_right" />
               <div id="inner2" className="inner">
                     <strong>2022년</strong>
                     <img src={require("./img/model_logo.png")} alt="main_kakaoimg" />
                     <small>패션 디자이너의 꿈을 가진 청소년</small>
                     <p>2022.04.01 ~ 04.16</p>
                     <ul>
                        <li onClick={() => navigate('/contest/model/info')}>콘테스트 소개</li>
                        <li onClick={() => navigate('/contest/model/application_info')}>접수 안내</li>
                        <li onClick={() => navigate('/contest/model/application')}>접수하기</li>
                     </ul>
               </div>
            </div>
         </MainImg> 
         </Container>
   );
};

export default Main;
