import styled, { keyframes } from 'styled-components';
//img
import AboutBg from "./img/main.png";

// arrow animation
const arrowAnimation = keyframes`
  0% {
   top:60%;
  }
  50% {
   top:65%;
  }
  100%{
   top:60%;
  }
`;

const lineAnimation = keyframes`
  0% {
   transform: translateX(-50%) translateY(-50%) rotate(45deg) scale(0.5);
  }
  33% {
   transform: translateX(-50%) translateY(-50%) rotate(15deg) scale(0.5);
  }
  66% {
   transform: translateX(-50%) translateY(-50%) rotate(0deg) scale(0.5);
  }
  100%{
   transform: translateX(-50%) translateY(-50%) rotate(-15deg) scale(0.5);
  }
`;
const innerAnimation = keyframes`
  0% {
   opacity:0;
  }
  33% {
   opacity:0;
  }
  66% {
   opacity:0;
  }
  100%{
   opacity:1;
  }
`;
const leftimgAnimation = keyframes`
  0% {
   opacity:0.5;
   top:-20%
  }
  33% {
   opacity:1;
   top:-10%
  }
  66% {
   opacity:1;
   top:0%
   }
  100%{
   opacity:1;
   top:0%
  }
`;
const rightimgAnimation = keyframes`
  0% {
   opacity:0.5;
   bottom:-20%
  }
  33% {
   opacity:1;
   bottom:-10%
  }
  66% {
   opacity:1;
   bottom:0%
  }
  100%{
   opacity:1;
   bottom:0%
  }
`;


// Main 

export const Container = styled.div`
   position: relative;
   height:300vh;
   ul.social {
      position: fixed;
      bottom:40px;
      left:2%;
      z-index: 99;
      li {
            position: relative;
            width:48px;
            height: 48px;
            border-radius:50%;
            background-color:rgba(255, 255, 255, 0.3);
            display: inline-block;
            margin-right:20px;
            a {
               width:48px;
               height: 48px;
               display: block;
            }
            img {
               width:65%;
               position: absolute;
               top:50%; left:50%;
               transform: translateX(-50%) translateY(-50%);
            }
      }
   }

`

// Main 
export const MainVideo = styled.div`
   width:100%;
   position: fixed;
   z-index: 99;
   top: 50%;
   left: 50%;
   transform: translate(-50%,-50%);
   transition: all .5s ease;
   .video {
      width:100%;
      height: 100vh;
      object-fit: cover;
      transform: scale(1);
   }
   .text {
      display: block;
      text-align: center;
      z-index: 9;
      position: absolute;
      top:50%;
      left:50%;
      transform: translateX(-50%) translateY(-50%);
      font-size:40px;
      font-weight:700;
      line-height: 68px;
   }
   .arrow{
      position: absolute;
      width: 30px;
      display: block;
      top:60%;
      left:50%;
      transform: translateX(-50%) translateY(-50%);
      animation:${arrowAnimation} 1.5s ease-in-out infinite;
   }
   
`
// Main 
export const MainbgImg = styled.div`
   background-image: url(${AboutBg});
   background-repeat: no-repeat;
   background-size: cover;
   width:100%;
   height: 100vh;      
   position: fixed;
`
// Main 
export const MainImg = styled.div`
   width:100%;
   height: 100vh;      
   position: fixed;
   #line {
      position: absolute;
      top:50%; left:50%;
      transform: translateX(-50%) translateY(-50%) rotate(45deg) scale(0.5);
      width: 2px;
      height: 103.5vh;
      background-image: linear-gradient(to bottom, rgba(0,0,0,0), rgba(255,255,255), rgba(0,0,0,0));
      z-index: 9;
      opacity: 0;

   }
   
   .left {
      display: inline-block;
      width:50%;
      height: 100vh;
      position: relative;
      #leftimg {
         position: absolute;
         opacity: 0;
         top:-20%; left:0;
         width:59.5vw;
         height: 100vh;
         object-fit: cover;
         transform: 0.25s ease-in;
      }
      .inner {
         opacity: 0;
         position:absolute;
         top:50%; left:50%;
         transform: translateX(-50%) translateY(-50%);
         text-align: center;
         font-size:24px;
         color:#287EFF;
         strong {
            display: block;
         }
         img {
            margin:20px 0;
         }
         small {
            font-size:12px;
            margin-bottom: 15px;
            display: block;
         }
         p {
            font-size: 20px;
            margin-bottom: 40px;
         }
         ul {
            li {
               cursor: pointer;
               width:110px;
               height: 38px;
               border-radius: 100px;
               background-color: #287EFF;
               color:#fff;
               font-size:16px;                       
               display: inline-block;
               margin-right:10px;
               line-height: 38px;
               &:last-child {
                  margin-right:0;
               }
               
            }
         }
      }
   }
   .right {
      display: inline-block;
      width:50%;
      height: 100vh;
      position: relative;
      #rightimg {
         position: absolute;
         opacity: 0;
         bottom:-20%; right:0;
         width:59.5vw;
         height: 100vh;
         object-fit: cover;
         transform: 0.25s ease-in;
      }
      .inner {
         opacity: 0;
         position:absolute;
         top:50%; left:50%;
         transform: translateX(-50%) translateY(-50%);
         text-align: center;
         font-size:24px;
         color:#fff;
         strong {
            display: block;
         }
         img {
            margin:20px 0;
         }
         small {
            font-size:12px;
            margin-bottom: 15px;
            display: block;
         }
         p {
            font-size: 20px;
            margin-bottom: 40px;
         }
         ul {
            li {
               cursor: pointer;
               width:110px;
               height: 38px;
               border-radius: 100px;
               background-color: #fff;
               color:#000;
               font-size:16px;                       
               display: inline-block;
               margin-right:10px;
               line-height: 38px;

               &:last-child {
                     margin-right:0;
               }
            }
         }
      }
   }
`