import styled from "styled-components";


// Main 
export const Container = styled.div`
     width:100%;
    max-width: 960px;
    margin:0 auto;
    box-sizing: border-box;
    padding-top:80px;
    .underline {
        font-size: 34px;
        color:#fff;
        margin-bottom: 18px;
        position: relative;
        display: table;
        z-index: 9;
        &::before {
            content: '';
            position: absolute;
            width:100%;
            height: 12px;
            background-color: #ffe600c0;
            display: block;
            left:0; bottom: 0;
            z-index: -1;
        }
    }
    .con1 {
    }
    .con2 {
        ul {
            li {
                margin-bottom: 10px;
            }
        }
    }
    .con3 {
        max-width: 980px;
        table {
            width:100%;
            text-align: center;
            border-spacing: 0;
            margin-bottom: 30px;
            tr {
                height: 47px;
               }  
            thead {
                background-color: #333;
                tr {
                   td {
                        width:33.33%;
                   }
                }
            }
            tbody {
                tr {
                    td {
                        border-bottom: 1px solid #525252;
                    }
                }
            }
            .border {
                line-height: 27.2px;
                border-left:1px solid #525252;
                border-right:1px solid #525252;
            }
        }
    }
    .con6 {
        width:100%;
        overflow: hidden;
        .inner {
            width:100%;
            div {
                height: 130px;
                float: left;
            }
            span {
                display: block;
                margin-right:18px;
                display: inline-block;
                background-color: rgba(255, 255, 255, 0.2);
                border-radius: 10px;
                box-sizing: border-box;
                padding:24px 111px;
            }
            p {
                text-align: center;
                margin-top:8px;
            }
            .lastbox {
                margin-right:0;
                padding:27px 151px;
            }
        }
    }
`