import styled from "styled-components";


// Main 
export const Container = styled.div`
   width:100%;
   box-sizing: border-box;
/*  padding-top:80px; 는 header 때문에 꼭 필요한 요소 입니다. */
   padding-top:80px;
   text-align: left;
   .intro {
      margin-top:6px;
      margin-bottom: 20px;
      font-size: 32px;
      color:#fff;
      max-width: 1054px;
      margin:0 auto;
      margin-bottom: 20px;
   }
   #gnb_container{
      width: 100%;
      background-color: #000;
      z-index: 99;
      height: 31px;
      margin-bottom: 50px;
      .gnb {
         margin-bottom: 50px;
         max-width: 1054px;
         margin:0 auto 40px;
      li {
         cursor: pointer;
         display: inline-block;
         padding: 8px 10px;
         font-size: 15px;
         margin-right: 6px;
         border-radius: 100px;
         background-color: rgba(255, 255, 255, 0.50);
         a {
               color:#000;
         }
      }
      .show {
         background-color: #fff;
         color:#000;
      }
   }
   }
   
   .underline {
      font-size: 34px;
      color:#fff;
      margin-bottom: 18px;
      position: relative;
      display: table;
      z-index: 9;
      &::before {
         content: '';
         position: absolute;
         width:100%;
         height: 12px;
         background-color: #ffe600c0;
         display: block;
         left:0; bottom: 0;
         z-index: -1;
      }
   }
   .gallery {
      display: grid;
      gap: 8px 8px;
      grid-template-columns: repeat(auto-fill,calc(25% - 6px));
      li {
         position: relative;
         a {
               display: block;
         }
         span {
               display: block;
               background: rgba(255, 255, 255, 0.1);
               position: absolute;
               padding:24px 18px;
               left:0; bottom:0;
               width:100%;
               display: none;
               strong {
                  display: block;
                  margin-bottom: 10px;
               }
               p {
                  display: inline-block;
                  padding-right:10px;
                  position: relative;
                  font-size: 14px;
                  &::before {
                     content: '';
                     position: absolute;
                     right:0;
                     top:2px;
                     width:1px;
                     height: 10px;
                     background-color: #fff;
                  }
               }
               .no {
                  padding-left:10px;
                  &::before {
                     display: none;
                  }
               }
         }
         &:hover span {
               display: block;
         }
      }
   }

`