            <ul className="gnb" id="gnb" ref={gnb}>
               <li><Link to="con3" activeClass="active" spy={true} smooth={true} offset={-280} >작품주제</Link></li>
               <li><Link to="con4" activeClass="active" spy={true} smooth={true} offset={-280} >응모기간</Link></li>
               <li><Link to="application" activeClass="active" spy={true} smooth={true} offset={-280} >응모방법</Link></li>
               <li><Link to="note" activeClass="active" spy={true} smooth={true} offset={-280} >유의사항</Link></li>
            </ul>
