// 기본 적용 스타일 코드입니다.
import { createGlobalStyle } from "styled-components";

// 사용되는 폰트입니다.


const globalStyle = createGlobalStyle`
   :root {
      --main: #e94e4d;
      --sub:#777777;
      --darkmain: #d62e2d;
      --background: #ffffff;
      --white: #ffffff;
      --placeholder: #bbb19e;
      --line: #ded9d0;
   }

   /* SpoqaHanSansNeo font */
   
   * {
      margin: 0;
      padding: 0;
      box-sizing: border-box;
      line-height: 1;
   }
   html {
      font-size: 16px;
   }
   body {
      /* font-family: 'Spoqa Han Sans Neo', sans-serif; */
      -webkit-touch-callout: none;
      -webkit-user-select: text;
      -khtml-user-select: text;
      -moz-user-select: text;
      -ms-user-select: text;
      user-select: text;
      letter-spacing: -0.04em;
      background-color: #000000;
      -ms-overflow-style: none;
      &::-webkit-scrollbar{
         display:none;
      }
   }
   #root {
      position: relative;
      width: 100%;
      background-color: #000; 
      color:#fff;
   }
   a {
      text-decoration: none;
      color: inherit;
   }
   img {
      width: 100%;
   }
   ul, ol {
      list-style-type: none;
   }
   input,
   select,
   textarea {
      font-family: inherit;
      color: inherit;
      font-size: inherit;
      outline: none;
      resize: none;
      background-color: transparent;
      border-color:transparent
   }
   input[type="number"] {
      -moz-appearance: textfield;
   }
   input::-webkit-outer-spin-button,
   input::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
   }
   input[type="checkbox"] {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      height: 0;
      width: 0;
      :checked ~ .check {
         background-color: var(--main);
         &::after {
            display: block;
         }
      }
   }
   select {
      -moz-appearance:none; /* Firefox */
      -webkit-appearance:none; /* Safari and Chrome */
      appearance:none;
   }

   button {
      font-family: inherit;
      color: inherit;
      font-size: inherit;
      font-weight: inherit;
      border: none;
      background: none;
      outline: none;
      cursor: pointer;
   }
   .cursor {
      cursor: pointer;
   }
   h2{
      font-style: normal;
      font-weight: 700;
      font-size: 40px;
      letter-spacing: -0.04em;
      color: #434B5A;
   }
`;

export default globalStyle;
