import styled from "styled-components";


// Main 
export const Container = styled.div`
   width:100%;
   box-sizing: border-box;
   padding-top:80px;
   position: relative;
   .underline {
      font-size: 34px;
      color:#fff;
      margin-bottom: 18px;
      position: relative;
      display: table;
      z-index:9;
      &::before {
         content: '';
         position: absolute;
         width:100%;
         height: 12px;
         background-color: #ffe600c0;
         display: block;
         left:0; bottom: 0;
         z-index: -1;
      }
   }
    
   .con1 {
      max-width: 960px;
      margin:0 auto;
      padding-top:0;
      >p {
         margin-bottom:10px;
      }
      h4 {
         margin-bottom: 10px;
         font-size: 20px;
      }
      .inner {
         div {
               display: inline-block;
               widtH: 50%;
               vertical-align: top;
               &:nth-child(2) {
                  padding-left:18px;
                  box-sizing: border-box;
               }
               p {
                  font-size:14px;
                  line-height: 23.8px;
                  margin-bottom: 30px;
               }
         }
      }
   }
   .con2 {
      .inner {
         max-width: 960px;
         margin:0 auto;
         span {
               padding:20px;
               line-height: 30.6px;
               display: inline-block;
               margin-right:18px;
               background-color: rgba(255,255,255,0.2);
               width:471px;
               height: 102px;
               border-radius: 18px;
               margin-bottom: 18px;
               font-size:18px;
               &:nth-child(3) {
                  margin-right:0;
               }
         }
      }
      .models1 {
               padding-left:240px;
               display: block;
               height: 277px;
               object-fit: cover;
               margin-bottom: 18px;
      }
      .models2 {
               padding-right:240px;
               display: block;
               height: 277px;
               object-fit: cover;
      }
   }
   .con3 {
      max-width: 960px;
      margin:0 auto;
      margin-bottom: 70px;
      h2 {
         margin-bottom: 70px;}
      div {
         margin-bottom: 70px;
         h3 {
               font-size: 24px;
         
         }    
         >p {
               margin:18px 0;
               font-size: 18px;
         }
         ul {
               display: grid;
               gap: 18px 18px;
               grid-template-columns: repeat(auto-fill,calc(25% - 14px));
               li {
                  text-align: center;    
                  margin-bottom: 18px;
               }
               
         }
         h4 {
               margin-top:18px;
               margin-bottom: 10px;
         }
      }
      
   }
   .con4 {
      max-width: 960px;
      margin:0 auto;
      h2 {
         margin-bottom: 70px;
      }
      h3 {
         font-size: 24px;
      }    
      >p {
         margin:18px 0;
         font-size: 18px;
      }
      ul {
         display: grid;
         gap: 18px 18px;
         grid-template-columns: repeat(auto-fill,calc(25% - 14px));
         li {
               text-align: center;    
               margin-bottom: 18px;
         }
      }
      h4 {
         margin-top:18px;
         margin-bottom: 10px;
      }
   }
   .support {
      max-width: 960px;
      margin:0 auto;
      margin-bottom: 162px;
      h2 {
         margin-bottom: 30px;
      }
      ul {
         display: grid;
         gap: 18px 18px;
         grid-template-columns: repeat(auto-fill,calc(25% - 14px));
      }
   }

   /* map */
   .map {
      max-width: 960px;
      margin:0 auto;
      margin-bottom: 100px;
      #location {
         width: 100%;
         height: 380px;
         margin-bottom: 10px;
      }
      h2 {
         font-size:28px;
         margin-bottom: 36px;
         color:#fff;
      }
      .box {
         padding:20px;
         background-color:rgba(255,255,255,0.2);
         border-radius: 10px;
         margin-bottom: 10px;
         strong {
               font-size:20px;
               margin-bottom: 10px;
               display: block;
         }
         p {
               font-size:18px;
               line-height: 30px;
         }
      }
   }

`