import styled from "styled-components";


// Main 
export const Container = styled.div`
/*  padding-top:80px; 는 header 때문에 꼭 필요한 요소 입니다. */
    padding-top:80px;
    width:100%;
    max-width: 960px;
    margin:0 auto;
    box-sizing: border-box;
    .intro {
        margin-top:6px;
        margin-bottom: 20px;
        font-size: 32px;
        color:#fff;
        max-width: 1054px;
        margin:0 auto;
        margin-bottom: 20px;
    }
    .notice {
        li {
            padding:30px 0;
            border-bottom: 1px solid #525252;
            position: relative;
            &:last-child {
                border-bottom: none;
            }
            span {
                float: right;
                padding-right: 2%;
            }
            &::before {
                content: '';
                width:1px;
                height: 10px;
                background-color: #fff;
                transform: rotate(-45deg);
                position: absolute;
                top:29px;
                right:0;
            }
            &::after {
                content: '';
                width:1px;
                height: 10px;
                background-color: #fff;
                transform: rotate(45deg);
                position: absolute;
                top: 36px;
                right:0;
            }
        }
    }
    .num {
        margin-top:58px;
        text-align: center;
        position: relative;
        width: 27%;
        margin: 0 auto;
        margin-top: 58px;
        li {
            display:inline-block;
            margin-right:14px;
            &:last-child {
                margin-right:0;
            }
            a {
                color:#727272
            }
        }
        span {
            display: inline-block;
            position: relative;
            vertical-align: middle;
            &::before {
                content: '';
                width:1px;
                height: 10px;
                background-color: #fff;
                transform: rotate(-45deg);
                position: absolute;
                top:0;
                right:0;
            }
            &::after {
                content: '';
                width:1px;
                height: 10px;
                background-color: #fff;
                transform: rotate(45deg);
                position: absolute;
                top:7px;
                right:0;
            }
            a {
                width:20px;
                height: 20px;
                display: block;
            }
        }
        .show {
            a {
                color:#fff;
            }
        }
    }

`