import React, { useState, useEffect } from "react";

// utils

const MainLogic = () => {
   const [state, setState] = useState({
     step:1,
   });

    // state 셋팅
    const setStateValue = (key, value) => {
      setState({ ...state, [key]: value });
   };

 
   return { state, func :{setStateValue}};
};

export default MainLogic;
