import styled from "styled-components";


// Main 
export const Container = styled.div`
      width:100%;
    max-width: 960px;
    margin:0 auto;
    box-sizing: border-box;
    padding-top:80px;
    .application {
        margin-bottom: 60px;
        h2 {
            text-align: center;
            font-size:32px;
            color:#fff;
            margin-top:78px;
            margin-bottom: 30px;
        }
        >div {
            padding:20px;
            background-color: rgba(255, 255, 255, 0.2);
            border-radius: 10px;
            margin-bottom: 18px;
            span {
                width:26px;
                height: 26px;
                background-color: #fff;
                line-height: 26px;
                text-align: center;
                display: inline-block;
                color:#000;
                border-radius: 100%;
                vertical-align: top;
            }
            .text {
                display: inline-block;
                margin-left:14px;
                >a {
                    font-size: 14px;
                }
                p {
                    margin-bottom: 5px;
                    font-weight: 700;
                    &:last-child {
                        margin-bottom: 0;
                        font-size: 14px;
                        font-weight: 400;
                        line-height: 22.5px;
                    }
                }
            }
            .link {
                display: block;
                float: right;
                padding:10px 8px;
                background-color: #747474;
                border-radius: 5px;
                font-size:14px;
            }
        }
    }
    .note {
        margin-bottom: 162px;
        h3 {
            font-size: 22px;
            margin-bottom: 18px;
        }
        ul {
            li {
                line-height: 27px;
            }
        }
    }
`