// 공통으로 사용되는 스타일 컴포넌트 모음 파일입니다.
import styled from "styled-components";

// TitleSection : gnb 부분
export const TitleSection = styled.div`
   top: 74px;
   width: 100%;
   background-color: #000;
   z-index: 11;
   .main_name {
      margin-top:78px;
      font-size: 16px;
      color:#fff;
   }
   .intro {
      margin-top:6px;
      margin-bottom: 20px;
      font-size: 32px;
      color:#fff;
      &.notice{
         margin-top:40px;
      }
   }
   .gnb {
      width: 100%;
      background-color: #000;
      z-index: 99;
      li {
         cursor: pointer;
         display: inline-block;
         margin-right:8px;
         a {
            color:#000;
            display: inline-block;
            width: 100%;
            height: 100%;
            padding:8px 14px;
            background-color: rgba(255, 255, 255, 0.50);
            border-radius: 100px;
            &.active{
               background-color: #fff;
            }
         }
      }
   }
   &#information{
      background-color: #000;
      width: 100vw;
      height: 31px;
      z-index: 99;
      margin-bottom: 50px;
      padding-top:90px ;
      .gnb {
         max-width: 960px;
         margin:0 auto;
      }
   }
   
`;

// TitleSection
export const ContentSection = styled.div`
   > div{
      padding-top:90px;
      &:nth-child(1){
         padding-top:50px;
      }
      &:last-child{
         margin-bottom: 300px;
      }
   }
`;